"use strict";
'use strict';

//file system 

var fs = require('fs');
fs.readFile('./index2.html', function (err, data) {
    if (err) throw err;
    console.log(data.toString());
});
'use strict';

//http.js
var http = require('http');
var fs = require('fs');
var file = '';
var onRequest = function onRequest(request, response) {
    console.log("Request received.");
    response.writeHead(200, { "Content-Type": "text/Plain" });
    response.writeHead(200, { "charset": "utf-8" });
    // response.write("Hello world");
    response.write(file);
    response.end();
};

fs.readFile('../index2.html', function (err, data) {
    if (err) throw err;
    file = data.toString();
});

http.createServer(onRequest).listen(3000);
console.log("Serve has started");
'use strict';

function dd() {
    return 'dd';
}
module.exports = {
    a: function a(name) {
        return 'Hello ' + name;
    },
    b: { test: 12 },
    c: 123,
    d: 'test',
    e: false,
    f: new Date(),
    dd: dd
};
'use strict';

var path = require('path');
console.log(path.join(__dirname, 'good', 'job'));
